    margin: 0;
    padding: 0;
    color: #333;
  }
  
  /*navbar*/
  #nav-bar {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    background-color: rgb(255, 255, 255);
  }
  
  .nav-bar-item {
    display: inline-block;
  }
  
  #nav-bar a {
    display: inline-block;
    padding: 1.3em;
    text-decoration: none;
    font-size: 30px;
    color: #616161;
    margin-top: 50px;
  }
  
  #nav-bar a:hover {
    text-decoration: underline;
    color: #000;
  }
  
  .header {
    min-height: 56px;
    transition: min-height 0.3s;
  }
  
  .header .logo-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  
  .nav {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    background-color: rgb(255, 255, 255);
  }
  
  .nav__list {
    width: 0;
    padding: 0;
    margin-left: -60px;
  }
  
  .nav__item {
    box-sizing: border-box;
    display: inline-block;
    width: 24%;
    line-height: 24px;
    text-transform: uppercase;
    font-size: 25px;
  }
  
  .nav a {
    display: inline-block;
    padding: 1.3em;
    text-decoration: none;
    color: #616161;
  }
  
  .nav a:hover {
    text-decoration: underline;
    color: #000;
  }
  
  /*drawer*/
  #drawer {
    padding-left: 30px;
    width: 150px;
    height: 50%;
    position: absolute;
    transform: translate(-250px, 0);
    transition: transform 0.3s ease-in-out;
    background-color: #111;
    z-index: 999;
  }
  
  #drawer.open {
    transform: translate(0, 0);
  }
  
  /* styling pendukung */
  
  main {
