/*coba dulu*/
.restos {
  display: grid;
  grid-template-columns: repeat(4, 1fr);;
  gap: 16px;
  margin: 32px 0;
}

/*
    Movie Item
  */

.resto-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
  margin: 50px;
}

.resto-item__header {
  position: relative;
  margin: 40px;
}


.resto-item .resto-item__header .resto-item__header__poster {
  width: 100%;
  height: 300px; 
  object-fit: cover;
}

.resto-item .resto-item__header .resto-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: black;
  color: white;
}

.resto-item
  .movie-item__header
  .movie-item__header__rating
  .movie-item__header__rating__score {
  margin-left: 10px;
}

.resto-item .resto-item__content {
  padding: 16px;
}

.resto-item .resto-item__content h3 {
  margin: 0 0 10px 0;
}

.resto-item .resto-item__content h3 a {
  color: #db0000;
  text-decoration: none;
}

.resto-item .resto-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}

/*
    Movie
  */

.resto {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}

.resto .resto__poster {
  width: 100%;
  max-width: 400px;
}

.resto .resto__info h4 {
  margin: 8px 0;
}

  /*footer*/
  footer {
    background-color: #333;
    color: white;
    text-align: center;
    padding: 20px;
    bottom: 0;
    width: 100%;
  }
  
  .footer-content {
    display: inline-block;
    width: 100%;
    text-align: center;
  }
  
  .footer-content p {
    margin: 20px;
  }
  
  .footer-links {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .footer-links li {
    display: inline-block;
    margin-left: 10px;
  }
  
  .footer-links li:first-child {
    margin-left: 0;
  }
  
  .footer-links li a {
    color: white;
    text-decoration: none;
  }
  
  .footer-links li a:hover {
    border-bottom: 1px solid white;
  }
  
  .footer-content p {
    margin: 0;
  }
  
  #nav-bar {
    display: block;
  }
  
  #nav-bar ul {
    margin-left: 50px;
  }
  /*skip to konten*/
  .skip-link {
    position: absolute;
    font-size: 20px;
    transform: translateY(-200%);
    transition: transform 325ms ease-in;
  }
  
  .skip-link:focus {
    transform: translateY(0);
  }
  

  /*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
